import React, { Component } from "react";
import PropTypes from "prop-types";
import Dialog from "material-ui/Dialog";
import FlatButton from "material-ui/FlatButton";
import RaisedButton from "material-ui/RaisedButton";
import DatePicker from "material-ui/DatePicker";
import TextField from "material-ui/TextField";

const SaveFileModal = ({ isSaving, onSave, onCancel, textValue }) => {
  const handleSave = () => {
    const name = this.refs.input.getValue();
    const textValue = textValue;
    onSave(name, textValue);
    onCancel();
  };

  const mockSubmit = e => {
    e.keyCode === 13 && handleSave();
  };
  const actions = [
    <FlatButton
      label="Cancel"
      primary={true}
      keyboardFocused={true}
      onTouchTap={onCancel}
    />,
    <FlatButton
      label="Save"
      primary={true}
      keyboardFocused={true}
      onTouchTap={handleSave}
    />
  ];
  return (
    <div>
      <Dialog
        title="Please enter the filename"
        actions={actions}
        modal={false}
        open={isSaving}
        onRequestClose={handleClose}
        contentStyle={{
          width: "50vw"
        }}
        bodyStyle={{
          display: "flex",
          justifyContent: "center"
        }}
        titleStyle={{
          fontWeight: "300",
          fontSize: "2rem"
        }}
      >
        <TextField
          hintText="filename"
          fullWidth={true}
          onKeyDown={mockSubmit}
          ref="input"
        />
      </Dialog>
    </div>
  );
};

SaveFileModal.propTypes = {
  isSaving: PropTypes.bool,
  onSave: PropTypes.func,
  onCancel: PropTypes.func,
  textValue: PropTypes.string
};

export default SaveFileModal;
